*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.customContainer{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
}
.allDiceImg{
    max-width: 550px;
}

.heroImg{
    padding: 10px;
}

.heroHeading h1{
    font-size: 5rem;
}
.playNow{
    width: 180px;
    padding: 0.5em;
    background: #000;
    color: white;
    float: right;
    border-radius: 5px;
    font-weight: 400;
}
.playNow:hover{
    background-color: #1a1919;
    cursor: pointer;
    transform: scale(1.1, 1.1);
    transition: transform 0.2s;
}
.playNow:not(hover)
{
    transition: transform 0.2s;
}

@media (width < 1024px) {
    .allDiceImg{
        max-width: 450px;
    }
    .heroHeading h1{
        font-size: 4rem;
    }
}
@media (width < 900px) {
    .allDiceImg{
        max-width: 400px;
    }
    .heroHeading h1{
        font-size: 4rem;
    }
    .playNow{
        width: 150px;
        padding: 0.5em;
        font-weight: 350;
    }
}
@media (width <= 768px) {
    .customContainer{
        display: flex;
        justify-content: center;
        align-items: center;
        height: 100vh;
        flex-direction: column;
    }
    
    .heroHeading{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
}
@media (width <= 425px) {

    .allDiceImg{
        max-width: 350px;
    }

    .heroHeading h1{
        font-size: 3.5rem;
    }
    
}
@media (width <= 375px) {

    .allDiceImg{
        max-width: 300px;
    }
    
    .heroHeading h1{
        font-size: 3rem;
    }
    .playNow{
        width: 150px;
        padding: 0.2em;
        font-weight: 350;
    }
}