.upperContent{
    display: flex;
    width: 100%;
    justify-content: space-between;
    padding-left: 4rem;
    padding-right: 4rem;

}
.scoreBoard{
    text-align: center;
}
.scoreBoard h1{
    font-size: 4em;
    font-weight: bold;
}
.numberPanel{
    float: right;
    text-align: end;
    font-size: larger;
    font-weight: 400;
    display: flex;
    flex-direction: column;
    margin-top: 1rem;
    justify-content: inherit;
}
.btnHolder{
    display: flex;
    gap: 10px;
}
.btn{
    width: 50px;
    height: 50px;
    background-color: white;
    font-size: 1.1rem;
    font-weight: 900;
    border: 1px solid black;
}

.btn:focus{
    background-color: black;
    color: white;
}


.main{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height:calc(100vh - 122px);
    gap: 1rem;
}
.dice{
    width: 200px;
}

.note{
    font-size: larger;
    font-weight: 500;
}
.btn2{
    width: 200px;
    height: 2.2rem;
    background: white;
    border: 1px solid black;
    border-radius: 5px;
    font-weight: 600;
}
.btn2:hover{
    background: black;
    color: white;
}

@media (width <= 650px) {
    .upperContent{
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    .numberPanel{
        align-items: center;
    }
    .main{
        height:calc(100vh - 220px);
    }
    .dice{
        width: 150px;
    }
}
@media (width <= 375px) {
    .btn{
        width: 45px;
        height: 45px;
        font-size: 1rem;
        font-weight: 800;
    }
}
@media (width <= 340px) {
    
    .btnHolder{
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        grid-template-rows: repeat(2, 1fr);
    }
    .btn{
        width: 50px;
        height: 50px;
        font-size: 1.1rem;
        font-weight: 900;
    }
}